@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
.container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 300px 25px 250px 300px 100px;
}

#p1 {

  font-size: 20px;

}

.header {
    grid-column: 1/4;
    grid-row: 1/2;
    text-align: left;
    text-align: justify;
    font-family: 'Gamja Flower', cursive;
    font-size: 35px; 
    color: rgb(131, 4, 204);
    -webkit-text-stroke: .1px rgb(224, 155, 51);
    height: 300px;
    background-image: url(/static/media/toys.2c740343.jpeg);
}

#stripe {

  grid-column: 1/4;
  grid-row: 2/3;
  height: 25px;
  background-color: rgb(252, 230, 39);
  opacity: .8;
}

#lego {

  grid-column: 1/4;
  grid-row: 3/7;
  background-image: url(/static/media/lego.78b015fc.jpeg);
  background-size: cover;
  background-repeat: no-repeat;
  opacity: .8;
  z-index: -1;

}

#grid-one {

  padding: 200px;
  grid-column: 1/2;
  grid-row: 3/4;
  background-repeat: no-repeat;
  border-radius: 100%;
  
}

#grid-two {

    grid-column: 1/3;
    grid-row: 3/4;
    text-align: center;
    margin-top: 50px;
    margin-left: 50px;
    border: 75px;
    font-size: 25px;
    font-weight: bolder;
    color: black;
    background: rgb(245, 140, 140);
    opacity: .8;
    font-family: 'Gamja Flower', cursive;
}

#grid-three {

    grid-column: 1/3;
    grid-row: 4/5;
    text-align: center;
    margin-top: 50px;
    margin-left: 50px;
    border: 75px;
    font-size: 25px;
    font-weight: bolder;
    color: black;
    background: rgb(245, 140, 140);
    opacity: .8;
    font-family: 'Gamja Flower', cursive;
}






.login-form {
    grid-column-start: 3;
    grid-row-start: 2;
    grid-row-end: 2;
    max-width: 384px!important;
    border: 5px solid rgb(238, 255, 0);
    padding: 10px;
    border-radius: 10px;
    right: 5px;
    top: 5px;
    margin-left: auto;
    margin-right: 50px;
    margin-bottom: auto;
    margin-top: 200px;
    color: rgb(245, 112, 59);
    background-color:rgba(233, 245, 58, 0.8);
    font-family: 'Gamja Flower', cursive;
    font-size: 18px;
}

.child {

    background-color: rgba(233, 245, 58, 0.8);
    background-origin: content-box;
}

#userName {

    background-color:rgba(228, 245, 79, 0.7);
}

#password {

    background-color:rgba(242, 245, 79, 0.7);
}

.login-row {
    width: 100%;
}

.input-field {
    width: 100%;
    padding: 0;
    margin: 10px 0;
    outline: 0;
    border-width: 0 0 2px;
    border-color: purple;
}

.input-field:focus {
    border-color: blue;
}

.sign-in-btn {
    width: 100%;
    padding: 10px;
    color: black;
    background-color: rgb(255, 72, 0);
    font-family: 'Gamja Flower', cursive;
}

.error {
    background-color: red;
}
.childSelect {
    margin: .5em;
} 

