 }
.DonationDemographics {
    display: grid;
    grid-template-columns: 60px 1fr 1fr 1fr 60px;
    grid-auto-flow: row;
}

.card {
    display: grid;
    border: solid 1 black;
    border-radius: 5px;
    grid-column: 4;
    background: lavender;
    background: cover;
}
.outgoingCard{
    border: solid 2px black;
    border-radius: 5px;
    border-style: ridge;
    margin: 6px;
    background-color: lavender;
    box-shadow: 7px 7px 3px #2b2431;
    max-width: 200px;
}
.Outgoing{
    display:flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
   
    

}

.card-btn {
    border-top: 1px solid #96d1f8;
   background: #8a3f3f;
   background: -o-linear-gradient(top, #d6a11a, #8a3f3f);
   padding: 5px 10px;
   border-radius: 8px;
   box-shadow: rgba(0,0,0,1) 0 1px 0;
   text-shadow: rgba(0,0,0,.4) 0 1px 0;
   color: white;
   font-size: 14px;
   font-family: Georgia, serif;
   text-decoration: none;
   vertical-align: middle;
}
.card-btn:hover {
    border-top-color: #bf6724;
   background: #bf6724;
   color: #ccc;
}
.card-btn:active {
    border-top-color: #a82f2f;
   background: #a82f2f;
}
.ShippingLabel {
    position: absolute;
   background-color: white;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    color: black;
    

}

#container {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: auto;
    background-color: rgba(39, 5, 83, 0.75);
}



.container1 {
  display: grid;
   background:
   radial-gradient(#5a4b66 15%, transparent 15%) 0 0,
  radial-gradient(#52445c 15%, transparent 15%) 16px 16px,
  radial-gradient(rgba(255,255,255,.1) 15%, transparent 20%) 0 1px,
  radial-gradient(rgba(255,255,255,.1) 15%, transparent 20%) 16px 17px;
  background-size: 32px 32px;
  background-color: #635270;
}
.notification {
    color: white;
    text-decoration: none;
    padding: 15px 26px;
    position: relative;
    display: inline-block;
    border-radius: 2px;
    width: 75px;
    cursor: pointer;
  }
  
  .notification:hover {
    background: red;
  }
  
  .notification .badge {
    position: absolute;
    top: 0px;
    right: 0px;
    padding: 5px 10px;
    border-radius: 50%;
    background: red;
    color: white;
  }
.navbar {
  background-color: #222;
  height: var(--nav-height);
  color: white;
  position: fixed;
  z-index: 100;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-branding {
  text-decoration: none;
  background-color: var(--primary-color);
  color: white;
  font-size: x-large;
  height: 100%;
  width: 270px;
  margin: 0 30px;
  display: flex;
  justify-content: flex-start;
}

.navbar-brand {
  display: flex;
  justify-content: flex-start;
  text-decoration: none;
  color: white;
  font-size: x-large;
  margin: auto;
}

.nav-list {
  list-style-type:none;
  align-items: center;
  display: flex;
  justify-content: flex-end;
  height: 100%;
}

.nav-item {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  cursor: pointer;

}

.nav-link:not(.active):hover {
  background-color: var(--primary-color);
  color: white;
}

.nav-link {
  text-decoration: none;
  color: white;
  padding: .5rem 1rem;
  cursor: pointer;
}

.nav-link.active {
  background-color: var(--secondary-color);
  border-radius: 15%;
}

.nav-space {
  opacity: 0.5;
}
.chat-section{
    position: fixed;
    max-width: 1100px;
    right: 0;
    bottom: 0;
    text-align: center;
    padding: 0 1rem;
}

.chat-btn{
    border: none;
    outline: none;
    cursor: pointer;
    position: fixed;
    right: 50px;
    bottom: 50px;
    background: dodgerblue;
    color: white;
    width:60px;
    height: 60px;
    border-radius: 50%;
    opacity: 0.8;
    transition: opacity 0.3s;
    box-shadow: 0 5px 5px rgba(0,0,0,0.4);
    display: flex;
    align-items: center;
    justify-content: center;
}

.chat-btn:hover, .submit:hover, #emoji-btn:hover{
    opacity: 1;
}

.chat-popup{
    position: fixed;
    bottom:80px;
    right:120px;
    height: 400px;
    width: 300px;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0.75rem;
    box-shadow: 5px 5px 5px rgba(0,0,0,0.4);
    border-radius: 10px;
}

.show{
    display: flex;
}

.chat-area{
    height: 80%;
    overflow-y: auto;
    overflow-x: hidden;
}

.income-msg{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.avatar{
    width:45px;
    height: 45px;
    border-radius: 50%;
    object-fit: cover;
}

.income-msg .msg{
    background-color: dodgerblue;
    color: white;
    padding:0.5rem;
    border-radius: 25px;
    margin-left: 1rem;
    box-shadow: 0 2px 5px rgba(0,0,0,0.4);
}

.input-area{
    position: relative;
    display: flex;
    justify-content: center;
}

input[type="text"]{
    width:100%;
    border: 1px solid #ccc;
    font-size: 1rem;
    border-radius: 5px;
    height: 2.2rem;
}

#emoji-btn{
    position: absolute;
    font-size: 1.2rem;
    background: transparent;
    right: 50px;
    top:2px;
    opacity:0.5;
}

.submit{
    padding: 0.25rem 0.5rem;
    margin-left: 0.5rem;
    background-color: green;
    color:white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    opacity: 0.7;
}


.out-msg{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.my-msg{
    display: flex;
    justify-content: flex-end;
    margin: 0.75rem;
    padding: 0.5rem;
    background-color: #ddd;
    border-radius: 25px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.4);
    word-break: break-all;
}

.feedback{
    width: 100%;
    color: blue;
}

.output{
    width: 100%;
}


.chat-btn:hover {
    background: red;
}

.badge {
    position: absolute;
    top: 0px;
    right: 0px;
    padding: 5px 10px;
    border-radius: 50%;
    background: red;
    color: white;
}

@media (max-width:500px){

    .chat-popup{
        bottom: 120px;
        right:10%;
        width: 80vw;
    }
}

.footer {
  display: flex;
  flex-direction: column;
  flex: 1 0 auto;
  background: #E5E5E5;
  flex-shrink: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: x-small;
  position: fixed;
  bottom: 0;
  height: 15px;
  width: 100%;
}

.footer span {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 3px;
  background: #B8B8B8;
}

:root {
  --nav-height: 50px;
  --primary-color: rgb(53, 137, 216);
  --secondary-color: rgb(32, 178, 170);
}

* {
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  min-width: 380px;
  width: auto !important;
}

